.contact-page {
    //adjust top margin for psuedo fancy tags 
    top: 9%;
}

.contact-form {
    width: 100%;
    margin-top: 20px;

    ul {
        padding: 0;
        margin: 0;

        li {
            display: block;
            position: relative;
            padding: 0;
            margin: 0;
            list-style: none;
            margin-bottom: 10px;
            overflow: hidden;
            //fade in animation, moves upwards on pageload
            opacity: 0;
            animation: fadeInUp 2s 2s;
            animation-fill-mode: forwards;
            //???
            clear: both;
        }

        //name left half email right half
        li.half {
            width: 49%;
            margin-left: 2%;
            float: left;
            //???
            clear: none;

            //???
            &:first-child {
                margin-left: 0;
            }
        }
    }

    //input boxes
    input[type='text'],
    input[type='email'] {
        width: 100%;
        border: 0;
        background: #115173;
        height: 50px;
        font-size: 16px;
        color: #f1f0ea;
        padding: 0 20px;
        box-sizing: border-box;
    }

    textarea {
        width: 100%;
        border: 0;
        background: #115173;
        height: 50px;
        font-size: 16px;
        color: #f1f0ea;
        padding: 20px;
        min-height: 150px;
        box-sizing: border-box;
    }

    //send button
    .flat-button {
        color: #c8ff00;
        background: 0 0;
        font-size: 11px;
        letter-spacing: 3px;
        text-decoration: none;
        padding: 8px 10px;
        border: 1px solid #c8ff00;
        //right side of form
        float: right;
        border-radius: 4px;
    }
}

//setup right of the contact form
.info-map {
    position: absolute;
    background-color: #000000;
    top: 5%;
    right: 2%;
    z-index: 900;
    width: 200px;
    padding: 20px;
    color: #f1f0ea;
    font-family: 'Helvetice';
    font-size: 17px;
    font-weight: 300;
    opacity: 0;
    animation: fadeIn 1s 1.5s;
    animation-fill-mode: forwards;

    span {
        font-size: 16px;
        display: block;
        padding-top: 20px;
        color: #c8ff00;
    }
}

//actual map
.map-wrap {
    background: rgba(8,253, 216, 0.1);
    float: right;
    width: 53%;
    height: 100%;
}

.leaflet-container {
    width: 100%;
    height: 100%;
    position: relative;
    opacity: 0;
    animation: backInRight 1s 1.2s;
    animation-fill-mode: forwards;
}




/** MOBILE */
@media screen and (max-width: 1200px) {
    .map-wrap {
        float: none;
        width: 100%;
        height: 400px;
        margin: 0;
    }

    //get rid of info map
    .info-map {
        display: none;
    }
}